#screen-setting {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-family: "retro", sans-serif;

  h2{
    color: white;
    font-family: "retro", sans-serif;
    font-size: 3em;
    cursor: default;
  }

  div{
    padding: 6px 0px 6px 0px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;

    input {
      border: 4px solid white;
      background: black;
      color: white;
      font-family: "retro", sans-serif;
      outline: none;
    }
  }

  #controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    span {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}
